	export default {
		data() {
			return {
				msgList: [],
				contentFlag: false,
				id: '',
				userInfo: {
					name: '',
					age: '',
					gender: ''
				},
				resultArr: []
			};
		},
		onLoad(e) {
			console.log(e);
			this.id = e.rid
			
		},
        onShow() {
            this.getData()
        },
		computed: {
			avatar() {
				return uni.getStorageSync('userinfo').avatarPath
			}
		},
		methods: {
			getData() {
				this.$api.znwzRecord({
					id: this.id
				}).then(res => {
					if (res.status == "OK") {
						let data = res.data
						this.userInfo = {
							age: data.age,
							gender: data.gender,
							name: data.name
						}

						let arr = JSON.parse(data.znwzRecordMessage.content)
						this.msgList = arr
						this.resultArr = data.znwzRecordResults
						this.resultArr.forEach(item1 => {
							item1.info = JSON.parse(item1.info)
							if (item1.recordRecommends.length > 0) {
								item1.recordRecommends.forEach(item2 => {
									console.log(item2);
									item2.info = JSON.parse(item2.info)
								})
							}
						})
					}
					console.log(res);
				}).catch(err => {
					console.log(err);
				})
			},
			productDetail(id) {
				uni.navigateTo({
					url: "/pages/health-product-detail/health-product-detail?id=" + id+'&showNav=false'
				})
			},
			topay(item) {
				console.log(item);
				if (!item.orderId) {
					// uni.removeStorageSync('community_cartList');
					let cartList = [];
					let obj = {
						id: item.productId,
						image: JSON.parse(item.info.icon)[0].url,
						title: item.info.name,
						
						price: item.info.price / 100,
						number: item.num
					};
                    console.log(obj)
					cartList.push(obj)
                    
					uni.setStorage({
						key: 'buylist',
						data: cartList,
						success: () => {
							uni.navigateTo({
								url: '/pages/pay/confirm/confirm?type=community&instanceId=' + item.id
							})
						}
					})
				}
			}
		},
	}
